.about {
    width:60%;
    margin: 0 60px;
    height: 75px;
    line-height: 75px;
    position: relative;
    z-index: 500;
}

.about__profile-image {
    margin-right: 30px;
    width: 75px;
    left: 64px;
}

.about__profile-image img {
    display: block;
}

.about__profile-image--rounded {
    border-radius:24px;
}

.about__display-name {
    font-size: 24px;
    left: 174px;

}
.about__email-address {
    font-size: 9px;
    margin-left: 8px;
    left: 264px;
}

.about__display-name,.about__profile-image,.about__email-address {
    height: 75px;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%)
}


